<template>
  <b-card
    no-body
    class="mb-0"
  >
    <b-modal
      v-model="modalShow"
      header-bg-variant="primary"
      no-close-on-esc
      no-close-on-backdrop
      hide-header-close
      hide-footer
      centered
    >
      <template
        #modal-header="{ close }"
        class="text-center"
      >
        <h4 class="text-white">
          Registro de asistencia
        </h4>
        <b-button
          size="sm"
          @click="close()"
        >
          X
        </b-button>

      </template>
      <div class="text-center my-4">
        <b-form-group>
          <b-form-input
            v-model="dia"
            type="date"
            required
            @input="setFechaRegistro"
          />
        </b-form-group>
        <b-button
          v-if="!spinnerShow"
          variant="danger"
          @click="onIniciarAsistencia"
        >
          Iniciar
        </b-button>
        <b-spinner
          v-else
          style="width: 5rem; height: 5rem;"
          variant="primary"
          label="Text Centered"
        />
      </div>
    </b-modal>

    <div class="m-2">
      <b-form @submit.prevent="onSubmit">
        <b-row class="mb-1">
          <b-col><b-form-input
            v-model="fecha_registro"
            type="date"
            required
            @input="actualizarTabla"
          /></b-col>
          <b-col><div id="reloj">
            HORA: {{ momentoActual }}
          </div></b-col>
          <b-col>
            <b-form-select
              v-model="comida"
              class="text-uppercase"
              :options="options"
              required
              @input="actualizarTabla"
            />
          </b-col>
          <b-col><b-button
            variant="outline-primary"
            block
            @click="actualizarRegistroAsistencia"
          ><feather-icon
            icon="RefreshCwIcon"
            class="mr-25"
          />
            Actualizar
          </b-button></b-col>
        </b-row>
        <!-- <b-row
        align-h="between"
        class="mb-1"
      >
        <b-col cols="3">
          <b-button
            variant="outline-primary"
            block
            size="sm"
            @click="actualizarRegistroAsistencia"
          >
            <feather-icon
              icon="RefreshCwIcon"
              class="mr-25"
            />
            Actualizar
          </b-button>
        </b-col>
        <b-col cols="3">
          <div id="reloj">
            HORA: {{ momentoActual }}
          </div>
        </b-col>
      </b-row> -->

        <b-row>
          <b-col md="4">

            <!-- <b-form-group
              label="Comida"
              label-class="text-uppercase font-weight-bold"
            >
              <b-form-select
                v-model="comida"
                class="text-uppercase"
                :options="options"
                @input="actualizarTabla"
              />
            </b-form-group> -->
            <b-form-group
              label-class="text-uppercase font-weight-bold"
              label="Ingresar DNI"
            >
              <b-form-input
                v-model="numero_documento"
                placeholder="Ingresar DNI"
                required
              />
            </b-form-group>
            <b-button
              type="submit"
              variant="primary"
              block
            >
              Registrar
            </b-button>
          </b-col>

          <b-col>
            <b-card
              :header="dataResponse.mensaje"
              :header-bg-variant="cardColor"
              header-text-variant="light"
              header-class="text-uppercase font-weight-bold"
              :border-variant="cardColor"
            >
              <b-card-body>
                <!-- DNI -->
                <b-row>
                  <b-col cols="5">
                    DNI:
                  </b-col>
                  <b-col cols="7">
                    {{ dataResponse.asistencia.persona.numero_documento }}
                  </b-col>
                </b-row>

                <!-- Nombre y apellidos -->
                <b-row>
                  <b-col cols="5">
                    Nombres y apellidos:
                  </b-col>
                  <b-col cols="7">
                    {{ dataResponse.asistencia.persona.nombre_completo }}
                  </b-col>
                </b-row>

                <!-- Fecha y hora de registro -->
                <b-row>
                  <b-col cols="5">
                    Fecha de registro:
                  </b-col>
                  <b-col cols="7">
                    {{ dataResponse.fecha_registro }}
                  </b-col>
                </b-row>
                <!-- Fecha y hora de registro -->
                <b-row>
                  <b-col cols="5">
                    Comida:
                  </b-col>
                  <b-col
                    cols="7"
                    class="text-uppercase"
                  >
                    {{ dataResponse.comida }}
                  </b-col>
                </b-row>

              </b-card-body>

            <!-- {{ dataResponse.usuario.name }} <br>
            {{ dataResponse.fecha_registro }} <br>
            {{ dataResponse.mensaje }} -->
            </b-card>
          </b-col>
        </b-row>
      </b-form>
    </div>

    <b-table
      id="my-table"
      ref="refTablaAsistencia"
      class="position-relative"
      show-empty
      head-variant="light"
      hover
      small
      responsive
      :busy.sync="isBusy"
      :items="myProvider"
      :fields="fields"
      :current-page="currentPage"
      :empty-text="'No hay registros para mostrar'"
      :empty-filtered-text="'No hay registros que coincidan con su solicitud.'"
      :per-page="perPage"
      :filter="filter"
      :filter-included-fields="filterOn"
      :sort-by="sortBy"
      :sort-desc="sortDesc"
    >
      <template #table-busy>
        <div class="text-center text-danger my-2">
          <b-spinner class="align-middle" />
          <strong>Cargando...</strong>
        </div>
      </template>

    </b-table>

    <div class="mx-2 mb-2">
      <b-row>
        <b-col
          cols="12"
          sm="6"
          class="d-flex align-items-center justify-content-center justify-content-sm-start"
        >
          <span class="text-muted">Mostrando registros del {{ from }} al {{ to }} de un total de {{ totalRows }} registros</span>

        </b-col>
        <!-- Pagination -->
        <b-col
          cols="12"
          sm="6"
          class="d-flex align-items-center justify-content-center justify-content-sm-end"
        >

          <b-pagination
            v-model="currentPage"
            :total-rows="totalRows"
            :per-page="perPage"
            first-number
            last-number
            class="mb-0 mt-1 mt-sm-0"
            prev-class="prev-item"
            next-class="next-item"
          >
            <template #prev-text>
              <feather-icon
                icon="ChevronLeftIcon"
                size="18"
              />
            </template>
            <template #next-text>
              <feather-icon
                icon="ChevronRightIcon"
                size="18"
              />
            </template>
          </b-pagination>

        </b-col>
      </b-row>
    </div>
  </b-card>
</template>

<script>
import {
  BForm, BFormGroup, BFormInput, BButton, BCard, BTable, BSpinner, BPagination, BRow, BCol, BFormSelect, BCardBody,
} from 'bootstrap-vue'
import { onUnmounted } from '@vue/composition-api'
import Ripple from 'vue-ripple-directive'
import store from '@/store'
import asistenciaBecariosStoreModule from './asistenciaBecariosStoreModule'

export default {
  components: {
    BForm, BFormGroup, BFormInput, BButton, BCard, BTable, BSpinner, BPagination, BRow, BCol, BFormSelect, BCardBody,
  },
  directives: {
    Ripple,
  },
  data() {
    return {
      fecha_registro: '2021-12-06',
      momentoActual: null,
      dia: null,
      cardColor: 'light',
      modalShow: false,
      spinnerShow: false,
      options: ['desayuno', 'almuerzo', 'cena', 'otros'],
      selected: null,
      numero_documento: null,
      comida: null,
      dataResponse: {
        mensaje: '.',
        fecha_registro: null,
        comida: null,
        usuario: {
          name: '',
        },
        asistencia: {
          persona: {
            nombre_completo: null,
          },
        },
      },
      dataResponseDefault: {
        mensaje: null,
        fecha_registro: null,
        comida: null,
        usuario: {
          name: '',
        },
        asistencia: {
          persona: {
            nombre_completo: null,
          },
        },
      },
      isBusy: false,
      mostrar: false,
      items: [],
      fields: [
        { key: 'persona.nombre_completo', label: 'comensal', sortable: true },
        {
          key: 'comida', label: 'comida', sortable: true, class: 'text-uppercase',
        },
        { key: 'fecha_registro', label: 'fecha_registro', sortable: true },
      ],
      from: 1,
      to: 1,
      totalRows: 1,
      currentPage: 1,
      perPage: 10,
      pageOptions: [5, 10, 15, 100],
      sortBy: 'id',
      sortDesc: false,
      sortDirection: 'desc',
      filter: '',
      filterOn: [],
    }
  },
  created() {
    this.horaActual()
    setInterval(this.horaActual, 1000)
    this.fecha_registro = `${new Date().getFullYear()}-${new Date().getMonth()}-${new Date().getDate()}`
  },
  methods: {
    setFechaRegistro() {
      this.fecha_registro = this.dia
    },
    actualizarRegistroAsistencia() {
      this.modalShow = true
    },
    horaActual() {
      setTimeout(() => {
        const fecha = new Date() // Actualizar fecha.
        let hora = fecha.getHours() // hora actual
        let minuto = fecha.getMinutes() // minuto actual
        let segundo = fecha.getSeconds() // segundo actual
        if (hora < 10) { // dos cifras para la hora
          hora = `0${hora}`
        }
        if (minuto < 10) { // dos cifras para el minuto
          minuto = `0${minuto}`
        }
        if (segundo < 10) { // dos cifras para el segundo
          segundo = `0${segundo}`
        }
        // devolver los datos:
        this.momentoActual = `${hora}:${minuto}:${segundo}`
        return this.momentoActual
      }, 1000)
    },

    actualizarTabla() {
      this.$refs.refTablaAsistencia.refresh()
    },
    success(message) {
      this.$swal({
        title: '!Éxito!',
        text: `${message}`,
        icon: 'success',
        customClass: {
          confirmButton: 'btn btn-primary',
        },
        buttonsStyling: false,
      })
        .then(() => {
          // console.log(resul)
          this.modalShow = false
        })
    },
    onIniciarAsistencia() {
      this.spinnerShow = true
      store.dispatch('asistencia/crearRegistroAsistencia', this.dia)
        .then(response => {
          this.spinnerShow = false
          this.success(response.data)
          // console.log(response)
        })
    },
    onSubmit() {
      const formData = {
        numero_documento: this.numero_documento,
        comida: this.comida,
        fecha_registro: `${this.fecha_registro} ${this.momentoActual}`,
      }

      store.dispatch('asistencia/addBecarioAsistencias', formData)
        .then(response => {
          this.cardColor = 'success'
          // this.dataResponse = response.data
          this.dataResponse = response.data.data
          this.dataResponse.comida = response.data.data.comida
          this.actualizarTabla()
        })
        .catch(error => {
          console.log('que paso', error.response)
          if (error.response.data.code === 422) {
            this.cardColor = 'warning'
            this.dataResponse = error.response.data.error.data
            this.dataResponse.comida = error.response.data.error.data.comida
          }
          if (error.response.data.code === 404) {
            this.cardColor = 'danger'
            this.dataResponse = Object.assign(this.dataResponseDefault, {})
            this.dataResponse.mensaje = error.response.data.error
          }
        })
    },
    async myProvider(ctx) {
      // eslint-disable-next-line no-param-reassign
      ctx.dia = this.fecha_registro
      // ctx.fecha_registro = `${new Date().getFullYear()}-${new Date().getMonth()}-${new Date().getDate()}`
      // eslint-disable-next-line no-param-reassign
      ctx.comida = [this.comida]
      // eslint-disable-next-line no-param-reassign
      ctx.asistencia = 1

      const promise = store.dispatch('asistencia/fetchBecarioAsistencias', { queryParams: ctx })
      return promise.then(response => {
        const {
          data, total, from, to,
        } = response.data

        const items = data
        this.totalRows = total || 0
        this.from = from || 0
        this.to = to || 0
        return items || []
      })
    },
  },
  setup() {
    const ASISTENCIA_APP_STORE_MODULE_NAME = 'asistencia'

    if (!store.hasModule(ASISTENCIA_APP_STORE_MODULE_NAME)) store.registerModule(ASISTENCIA_APP_STORE_MODULE_NAME, asistenciaBecariosStoreModule)

    onUnmounted(() => {
      if (store.hasModule(ASISTENCIA_APP_STORE_MODULE_NAME)) store.unregisterModule(ASISTENCIA_APP_STORE_MODULE_NAME)
    })

    return {

    }
  },
}
</script>

<style lang="scss" scoped>
#reloj {
  // background: #4B0B0C;
  font-size: 1.2em;
  font-weight: bold;
  color: #E70012;
  font-style: italic;
  padding: 2px;
  // border: #210007 5px solid;
  // border-radius: 10px;
  text-align: center;
}
</style>
